// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from './config/element-ui/element'
import App from './App'
import router from './router'
import store from './store/index'
import VueLazyload from 'vue-lazyload'

Vue.config.productionTip = false
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: 'static/error/9eb13cfdeb16a425add3fa4de7ff9bc1.jpg',
  loading: 'static/loading/5-160914192R4-50.gif',
  attempt: 1,
  throttleWait: 1000
})
Vue.use(ElementUI)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



