<template>
  <div id="home">
      <!-- header -->
      <div class="header">
        <home-head @navActive="getHeadActive" @contentListShow="getContentListShow"></home-head>
      </div>
      <!-- content -->
      <transition name="content-fade">
        <div id="post" class="content" v-if="isContentActive">
          <home-content :list-show="contentListShow"></home-content>
        </div>
      </transition>
      <!-- extra -->
      <!-- <div class="extra" style="border:1px dashed blue;width:100%;height:100px;"></div> -->
      <!-- footer -->
      <div class="footer" v-if="isContentActive">
        <home-foot></home-foot>
      </div>
  </div>
</template>

<script>
import Head from '@/components/common/Head'
// import Content from '@/components/home/Content'
import Foot from '@/components/common/Foot'
// 组件懒加载
const Content = () => import('@/components/home/Content')
export default {
  name: 'appHome',
  components: {
    'home-head': Head,
    'home-foot': Foot,
    'home-content': Content
  },
  data () {
    return {
      isContentActive: false,
      contentListShow: {}
    }
  },
  mounted () {
  },
  computed: {
  },
  methods: {
    getHeadActive (data) {
      this.isContentActive = data
      setTimeout(function () {
        document.getElementById('post').scrollIntoView({block: 'start', behavior: 'smooth'}) // , inline: 'nearest'
      }, 200)
    },
    getContentListShow (data) {
      this.contentListShow = data
    }
  }
}
</script>

<style scoped>
.content{
  position: relative;
  z-index: 1;
  width: 100%;
}
.content-fade-enter {
  transform: translateY(30px);
  opacity: 0.1;
}
.content-fade-enter-active{
  transition: all .6s ease 0s;
}
</style>



