<template>
  <div id="header">
    <!-- navgation -->
    <transition name="nav-fade">
    <div v-show="isHeaderShow">
      <head-nav :nav-move="isActive" :nav-top="isHeaderSTop" :nav-plat="isMobile"></head-nav>
    </div>
    </transition>
    <!-- introduction -->
    <div class="introduction">
      <div class="head-bg">
        <div class="bg-mask"></div>
      </div>
      <div class="head-content">
        <div class="content-item info">
          <!-- introduction <br>
          The Modern Element However players can deactivate this at any time. <br>
          Players can even travel across planets if they wish to enter a different environment. <br>
          Phone Apps customers can access their ... read more <br>
          scrollTop:{{curTop}}<br>
          clientHeight:{{clientHeight}}<br>
          scrollHeight:{{scrollHeight}}<br> -->
        </div>
        <div class="content-item load" @click="navActive" v-show="isNoActive">
          <i class="el-icon-arrow-down load-jump"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Carousel from '@/components/common/Swiper'
import Navgation from '@/components/common/Navgation'
export default {
  name: 'appHead',
  props: {
  },
  data () {
    return {
      isMobile: false,
      isActive: false,
      isNoActive: true,
      curTop: 0,
      preTop: 0,
      isHeaderShow: true,
      isHeaderSTop: false,
      loadStatus: true,
      rowShow: {
        row0: false,
        row1: false,
        row2: false,
        row3: false,
        row4: false
      },
      bottomHeight: 200,
      clientHeight: document.documentElement.clientHeight || document.body.clientHeight,
      scrollHeight: document.documentElement.scrollHeight || document.body.scrollHeight
    }
  },
  components: {
    // 'app-slick': Carousel
    'head-nav': Navgation
  },
  created () {
    if (this.isPhone()) {
      this.isMobile = true
    }
  },
  mounted () {
    // console.log(navigator.userAgent)
    // alert(navigator.userAgent)
    // alert(this.isMobile)
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeDestroy: function () {
    // console.log('beforeDestroy 钩子执行...销毁滚动条监听事件')
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    isPhone () {
      // phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|
      // MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone
      let str = 'Android|iPhone|iPad|Mobile'
      let mobile = navigator.userAgent.match(str)
      return mobile != null
      // return mobile
    },
    handleScroll () {
      // 导航条控制
      this.curTop = document.documentElement.scrollTop || document.body.scrollTop
      // console.log('当前滚动高度:' + this.curTop)
      // console.log('之前滚动高度:' + this.preTop)
      // console.log('窗口高度:' + this.clientHeight)
      if (this.curTop > this.clientHeight - 55 && this.curTop >= this.preTop) {
        this.isHeaderShow = false
        // console.log('隐藏头部')
        this.isHeaderSTop = false
        this.preTop = this.curTop
        // 内容加载控制
        this.contentLoad()
      } else {
        this.isHeaderShow = true
        // console.log('显示头部')
        this.isHeaderSTop = false
        this.preTop = this.curTop
        if (this.curTop === 0) {
          // 头部导航条回正
          this.isHeaderSTop = true
        }
      }
    },
    contentLoad () {
      // scrollTop:滑动高度 clientHeight：可视高度 scrollHeight：文档高度
      // console.log('Content Scroll监控:')
      // this.scrollHeight = document.documentElement.scrollHeight
      // console.log(this.scrollHeight, this.clientHeight, this.curTop)
      this.scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight
      if (this.scrollHeight - this.bottomHeight - this.clientHeight - this.curTop <= 0) {
        if (this.loadStatus && !this.rowShow.row0) {
          // console.log('row0')
          this.rowShow.row0 = true
        } else if (this.rowShow.row0 && !this.rowShow.row1) {
          // console.log('row1')
          this.rowShow.row1 = true
        } else if (this.rowShow.row1 && !this.rowShow.row2) {
          // console.log('row2')
          this.rowShow.row2 = true
        } else if (this.rowShow.row2 && !this.rowShow.row3) {
          // console.log('row3')
          this.rowShow.row3 = true
        } else if (this.rowShow.row3 && !this.rowShow.row4) {
          // console.log('row4')
          this.rowShow.row4 = true
          this.loadStatus = false
        }
        this.$emit('contentListShow', this.rowShow)
      }
    },
    navActive () {
      // 此处先加载服务端数据
      this.isActive = true
      this.isNoActive = false
      let that = this
      setTimeout(function () {
        that.$emit('navActive', that.isActive)
      }, 300)
    }
  }
}
</script>

<style scoped>
#header{
  color: aliceblue;
}
.nav-fade-enter, .nav-fade-leave-to{
  /* opacity: 0.1; */
  margin-top: -60px;
}
.nav-fade-enter-active, .nav-fade-leave-active {
  position: fixed;
  z-index: 9;
  transition: all .3s ease-in-out 0s;
}
.head-bg{
  position: absolute;
  z-index: 1;
  width: 100%;
  /* height: 100%; */
  height: 100vh;
  background: url('/static/image/computer.jpg') no-repeat center center scroll;
  background-size: cover;
}
.bg-mask{
  background-color:rgba(10, 10, 10, 0.7);
  width:100%;
  height:100%;
}
.head-content{
  position: relative;
  z-index: 2;
  height: 94vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  /* border:1px dashed yellow; */
}
/* .content-item{
  border:1px dashed yellow;
} */
.info{
  font-size: 18px;
}
.load{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  font-size: 25px;
  text-align: center;
  cursor: pointer;
}
.load:hover{
  /* background-color: rgba(18, 18, 19, .95); */
  /* box-shadow: 2px 2px 2px #000; */
  /*  ,-1px -1px 1px #000*/
  color: chocolate;
}
@keyframes jump{
  /* 0%   {margin-top:0px;}
  25%  {margin-top:-10px;}
  50%  {margin-top:0px;}
  75%  {margin-top:10px;}
  100% {margin-top:0px;} */
  from {margin-top: 10px}
  to {margin-top: -10px}
}
.load-jump{
  /* animation: jump 2s; */
  animation-name: jump;
  animation-duration: .5s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-play-state: running;
}
</style>



