<template>
  <div id="nav" v-bind:class="{'nav-active': navMove, 'nav-top': navTop}">
    <div class="pc" v-show="!navPlat">
      <!-- pc head -->
      <div class="nav-head">
        <div class="nav-head-item logo"  @click="handlerFullScreen">
          <span class="logo-text">老胡子</span>
          <!-- <img src="/static/image/white.png" alt="" height="48"> -->
          <!-- <span class="logo-text">Old Beard</span> -->
          </div>
        <div class="nav-head-item tab">首页|推荐|归档|友链|关于</div>
        <div class="nav-head-item icon">
          <div class="icon-item">
            <!-- primary / success / warning / danger / info -->
            <el-badge value="hot" :hidden="true" type="danger">
            <i class="el-icon-search"></i>
            </el-badge>
          </div>
          <div class="icon-item">
            <el-badge :value="100" :max="10">
              <i class="el-icon-bell"></i>
            </el-badge>
          </div>
          <div class="icon-item">
            <i class="el-icon-share"></i>
          </div>
          <div class="icon-item">
            <el-badge value="more" :hidden="true">
              <i class="el-icon-more"></i>
            </el-badge>
          </div>
        </div>
        <div class="nav-head-item user">
          <div class="user-item language">
            <div class>
              <span style="color:green">●</span>
              <!-- <el-button type="text" size="medium">中文</el-button> -->
              <span>简体中文</span>
            </div>
          </div>
          <div class="user-item login-in" v-if="login">
            <div class>
              <!-- <el-button type="text" size="medium">登录</el-button> -->
              <span>登入</span>
              <i class="el-icon-info"></i>
            </div>
          </div>
          <div class="user-item login-out" v-else>
            <div style="display:flex;align-items: center;" class="login-out-item item-username">
              <i class="el-icon-user"></i>
              <el-badge is-dot :hidden="false">
              <!-- <el-button type="text" size="medium">用户名</el-button> -->
              <span>老胡子吹风</span>
              </el-badge>
            </div>
            <div class="login-out-item">
              <!-- <el-button type="text" size="medium">退出</el-button> -->
              <span>退出</span>
            </div>
          </div>
        </div>
      </div>
      <!-- pc more -->
      <div class="nav-more" v-if="false">
        <div class>tab</div>
        <div class>tag</div>
        <div class>recent</div>
      </div>
    </div>
    <div class="mobile" v-show="navPlat">
      <!-- mobile head -->
      <div class="mobile-head">
        <!-- menu -->
        <div class="mobile-head-item">
          <!-- <i class="el-icon-more"></i> -->
          <el-badge is-dot :hidden="false" style="top:-1px;left:28px;z-index:1"></el-badge>
          <div id="nav-toggle"><span></span></div>
        </div>
        <!-- logo -->
        <div class="mobile-head-item mobile-logo"  @click="handlerFullScreen">
          <!-- <img src="/static/image/white.png" alt="" height="26"> -->
          <span class="logo-text">&nbsp;Old Beard</span>
          <!-- <span class="logo-text">&nbsp;老胡子</span> -->
        </div>
        <!-- icon -->
        <div class="mobile-head-item">
          <span style="color:#32b37b;font-size:6px">●&nbsp;</span>
          <!-- <el-button type="text" size="medium">语言</el-button> -->
          <span style="font-size:13px">English</span>
          <!-- <i class="el-icon-search"></i> -->
          <!-- <img src="/static/image/white.png" alt="" height="26"> -->
        </div>
      </div>
      <!-- monile more -->
      <div class="mobile-more" v-if="false">
        <div class>tab</div>
        <div class>tag</div>
        <div class>recent</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'navgation',
  props: {
    // title: String,
    // likes: Number,
    // isPublished: Boolean,
    // commentIds: Array,
    // author: Object,v-if="navPlat,""
    // callback: Function,
    // contactsPromise: Promise // or any other constructor
    navMove: Boolean,
    navTop: Boolean,
    navPlat: Boolean
  },
  data () {
    return {
      login: false
    }
  },
  mounted () {
    // console.log(document.fullscreenElement)
  },
  methods: {
    handlerFullScreen () {
      // -ms代表ie内核识别码
      // -moz代表火狐内核识别码
      // -webkit代表苹果的Safari浏览器及谷歌内核识别码
      // -o代表欧朋【opera】内核识别码
      if (!document.fullscreenElement && !document.mozFullScreenElement && !document.webkitFullscreenElement) {
        if (document.documentElement.requestFullscreen) {
          document.documentElement.requestFullscreen()
        } else if (document.documentElement.mozRequestFullScreen) {
          document.documentElement.mozRequestFullScreen()
        } else if (document.documentElement.webkitRequestFullscreen) {
          document.documentElement.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT)
        }
      } else {
        if (document.cancelFullScreen) {
          document.cancelFullScreen()
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen()
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen()
        }
      }
    }
  }
}
</script>
<style>
/* 修改ElementUI样式 */
.el-button--text {
  color: aliceblue;
}
.el-badge__content {
  border: 0px;
}
.el-badge__content.is-fixed{
  top: 4px;
}
</style>

<style scoped>
#nav {
  position: fixed;
  z-index: 9;
  width: 100%;
  padding-top: 30px;
  transition: all 0.3s ease 0s;
}
.nav-active {
  background-color: rgba(18, 18, 19, 0.95);
  /* background-color: rgba(51, 51, 51, 0.9); */
  box-shadow: 0px 5px 5px rgba(18, 18, 19, 0.5);
  padding-top: 0px !important;
}
.nav-top {
  background-color: rgba(18, 18, 19, 0);
  box-shadow: none;
  padding-top: 30px !important;
}
@media screen and (max-width: 900px){
  .mobile{
    display: block !important;
  }
  .pc{
    display: none;
  }
}
.nav-head {
  height: 60px;
  /* Flex 容器 */
  /* display: flex; */
  /* flex-direction: row | row-reverse | column | column-reverse; */
  /* flex-wrap: nowrap | wrap | wrap-reverse;*/
  /* flex-flow: <flex-direction> <flex-wrap>; */
  /* justify-content: flex-start | flex-end | center | space-between | space-around; */
  /* align-items: flex-start | flex-end | center | baseline | stretch; */
  /* align-content: flex-start | flex-end | center | space-between | space-around | stretch; */
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  /* justify-content: space-around; */
  align-items: stretch;
  /* align-content: center; */
  font-size: 16px;
}
.nav-head-item {
  /* Flex 项目 */
  /* order: <integer>; default 0 */
  /* flex-grow: <number>; default 0 */
  /* flex-shrink: <number>; default 1 */
  /* flex-basis: <length> | auto; default auto */
  /* flex: none | [ <'flex-grow'> <'flex-shrink'>? || <'flex-basis'> ] ; default 0 1 auto */
  /* align-self: auto | flex-start | flex-end | center | baseline | stretch; */
  padding: 0px 20px 0px 20px;
  /* border: 1px dashed white; */
  display: flex;
  align-items: center;
}
.logo {
  flex-grow: 1;
  font-size: 32px;
}
.tab {
  flex-grow: 4;
}
.pc i {
  /* border: 1px dashed red; */
  font-size: 19px;
  padding: 5px;
}
.icon {
  flex-grow: 1;
  display: flex;
  justify-content: space-around;
}
.icon-item {
  /* border: 1px dashed green; */
  display: flex;
  align-items: center;
}
.user {
  flex-grow: 1;
  display: flex;
}
.user-item {
  /* border: 1px dashed green; */
  flex-grow: 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
/* .login-in{
  border: 1px dashed yellow;
} */
/* .login-out{
  border: 1px dashed yellow;
} */
/* .item-username{
  font-family: 'yjmc';
} */
.nav-more {
  border: 1px dashed white;
}
.mobile-head {
  font-size: 21px;
  /* border: 1px dashed green; */
  height: 42px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  /* align-content: center; */
}
.mobile-head-item{
  padding: 0px 10px 0px 10px;
  /* border: 1px dashed yellow; */
  display: flex;
  align-items: center;
}
#nav-toggle span,
#nav-toggle span:before,
#nav-toggle span:after {
  /* cursor: pointer; */
  border-radius: 1px;
  height: 3px;
  width: 24px;
  background: #fff;
  position: relative;
  display: block;
  content: '';
}
#nav-toggle span:before {
  top: -6px;
}
#nav-toggle span:after {
  bottom: -3px;
}
.mobile-logo{
  display: flex;
  align-items: center;
}
@font-face {
  font-family: 'yjmc';
  src: url('/static/font/yjmc/yjmc.ttf');
  font-weight: normal;
  font-style: normal;
}
/* @font-face {
  font-family: 'btxb';
  src: url('/static/font/btxb/btxb.ttf');
  font-weight: normal;
  font-style: normal;
} */
.logo-text{
  font-family: 'yjmc';
}
</style>



